<template>
	<div id="edit-month">
		<form @submit.prevent="updateMonth"class="">
			<table>
				<thead class="orange darken-4 white-text">
					<tr>
						<th>Jan</th>
						<th>Feb</th>
						<th>Mar</th>
						<th>Apr</th>
						<th>May</th>
						<th>Jun</th>
						<th>Jul</th>
						<th>Aug</th>
						<th>Sep</th>
						<th>Oct</th>
						<th>Nov</th>
						<th>Dec</th>
					</tr>
				</thead>
				<tbody>
					<tr>
						<td>
							<input type="number" v-model="jan" required/>
						</td>
						<td>
							<input type="number" v-model="feb" required/>
						</td>
						<td>
							<input type="number" v-model="mar" required/>
						</td>
						<td>
							<input type="number" v-model="apr" required/>
						</td>
						<td>
							<input type="number" v-model="may" required/>
						</td>
						<td>
							<input type="number" v-model="jun" required/>
						</td>
						<td>
							<input type="number" v-model="jul" required/>
						</td>
						<td>
							<input type="number" v-model="aug" required/>
						</td>
						<td>
							<input type="number" v-model="sep" required/>
						</td>
						<td>
							<input type="number" v-model="oct" required/>
						</td>
						<td>
							<input type="number" v-model="nov" required/>
						</td>
						<td>
							<input type="number" v-model="dec" required/>
						</td>
					</tr>
				</tbody>
			</table>
			<br/>
			<div class="center">
				<button type="submit" class="btn btn-large">UPDATE</button>
			</div>
			<div class="fixed-action-btn">
				<router-link to="/" class="btn-floating btn-large red">
					<i class="fa fa-arrow-left"></i>
				</router-link>
			</div>
		</form>
	</div>
</template>

<script>
	import db from './firebaseInit'
	export default {
		name: 'edit-month',
		data () {
			return {
				month_id: null,
				jan: null,
				feb: null,
				mar: null,
				apr: null,
				may: null,
				jun: null,
				jul: null,
				aug: null,
				sep: null,
				oct: null,
				nov: null,
				dec: null,
			}
		},
		beforeRouteEnter (to, from, next) {
			db.collection('month').where('month_id', '==', to.params.month_id).get()
			.then(querySnapshot => {
				querySnapshot.forEach(doc => {
					next(vm => {
						vm.month_id = doc.data().month_id
						vm.jan = doc.data().jan
						vm.feb = doc.data().feb
						vm.mar = doc.data().mar
						vm.apr = doc.data().apr
						vm.may = doc.data().may
						vm.jun = doc.data().jun
						vm.jul = doc.data().jul
						vm.aug = doc.data().aug
						vm.sep = doc.data().sep
						vm.oct = doc.data().oct
						vm.nov = doc.data().nov
						vm.dec = doc.data().dec
					})
				})
			})
		},
		watch: {
			'$route': 'fetchData'
		},
		methods: {
			fetchData () {
				db.collection('month').where
				('month_id', '==', this.$route.params.month_id).get()
				.then(querySnapshot => {
					querySnapshot.forEach(doc => {
						this.month_id = doc.data().month_id
						this.jan = doc.data().jan
						this.feb = doc.data().feb
						this.mar = doc.data().mar
						this.apr = doc.data().apr
						this.may = doc.data().may
						this.jun = doc.data().jun
						this.jul = doc.data().jul
						this.aug = doc.data().aug
						this.sep = doc.data().sep
						this.oct = doc.data().oct
						this.nov = doc.data().nov
						this.dec = doc.data().dec
					})
				})
			},
			updateMonth () {
				db.collection('month').where('month_id', '==', this.$route.params.month_id).get()
				.then(querySnapshot => {
					querySnapshot.forEach(doc => {
						doc.ref.update({
							month_id: this.month_id,
							jan: this.jan,
							feb: this.feb,
							mar: this.mar,
							apr: this.apr,
							may: this.may,
							jun: this.jun,
							jul: this.jul,
							aug: this.aug,
							sep: this.sep,
							oct: this.oct,
							nov: this.nov,
							dec: this.dec
						})
						.then(() => {
							this.$router.push({name: 'dashboard', params: {month_id: this.month_id}})
						})
					})
				})
			}
		}
	}
</script>