<template>
	<div id="view-month">
		<table>
			<thead class="orange darken-4 white-text">
				<tr>
					<th>Jan</th>
					<th>Feb</th>
					<th>Mar</th>
					<th>Apr</th>
					<th>May</th>
					<th>Jun</th>
					<th>Jul</th>
					<th>Aug</th>
					<th>Sep</th>
					<th>Oct</th>
					<th>Nov</th>
					<th>Dec</th>
				</tr>
			</thead>
			<tbody>
				<tr>
					<td>{{jan}}</td>
					<td>{{feb}}</td>
					<td>{{mar}}</td>
					<td>{{apr}}</td>
					<td>{{may}}</td>
					<td>{{jun}}</td>
					<td>{{jul}}</td>
					<td>{{aug}}</td>
					<td>{{sep}}</td>
					<td>{{oct}}</td>
					<td>{{nov}}</td>
					<td>{{dec}}</td>
				</tr>
			</tbody>
		</table>
		<br/>
		<div class="center">
			<router-link v-bind:to="{name: 'edit-month', params: {month_id: month_id}}"class="btn btn-large">UPDATE</router-link>
			<!-- <button @click="deleteLogistic" class="btn btn-large red">DELETE</button> -->
		</div>
		<div class="fixed-action-btn">
			<router-link to="/" class="btn-floating btn-large red">
				<i class="fa fa-arrow-left"></i>
			</router-link>
		</div>
	</div>
</template>

<script>
	import db from './firebaseInit'
	export default {
		name: 'view-month',
		data () {
			return {
				month_id: null,
				jan: null,
				feb: null,
				mar: null,
				apr: null,
				may: null,
				jun: null,
				jul: null,
				aug: null,
				sep: null,
				oct: null,
				nov: null,
				dec: null,
			}
		},
		beforeRouteEnter (to, from, next) {
			db.collection('month').where('month_id', '==', to.params.month_id).get()
			.then(querySnapshot => {
				querySnapshot.forEach(doc => {
					next(vm => {
						vm.month_id = doc.data().month_id
						vm.jan = doc.data().jan
						vm.feb = doc.data().feb
						vm.mar = doc.data().mar
						vm.apr = doc.data().apr
						vm.may = doc.data().may
						vm.jun = doc.data().jun
						vm.jul = doc.data().jul
						vm.aug = doc.data().aug
						vm.sep = doc.data().sep
						vm.oct = doc.data().oct
						vm.nov = doc.data().nov
						vm.dec = doc.data().dec
					})
				})
			})
		},
		watch: {
			'$route': 'fetchData'
		},
		methods: {
			fetchData () {
				db.collection('month').where
				('month_id', '==', this.$route.params.month_id).get()
				.then(querySnapshot => {
					querySnapshot.forEach(doc => {
						this.month_id = doc.data().month_id
						this.jan = doc.data().jan
						this.feb = doc.data().feb
						this.mar = doc.data().mar
						this.apr = doc.data().apr
						this.may = doc.data().may
						this.jun = doc.data().jun
						this.jul = doc.data().jul
						this.aug = doc.data().aug
						this.sep = doc.data().sep
						this.oct = doc.data().oct
						this.nov = doc.data().nov
						this.dec = doc.data().dec
					})
				})
			}
		}
	}
</script>